import React from 'react'
import Layout from '../components/layout';
import Share from '../components/Share';
import {graphql} from 'gatsby';


export const query = graphql`
    query($slug: String!) {
        site {
            siteMetadata {
              url
              twitterHandle
              title
              description
              author
            }
          }
        markdownRemark(fields: {slug : { eq: $slug }}) {
            fields{
                slug
            }
            frontmatter {
                title
                date(formatString: "YYYY-MM-DD HH:mm")
                author
                tags

            }
            html
        }
    }
`
const BlogPost = (props) => {
    return (
        <Layout>
            <h3>{props.data.markdownRemark.frontmatter.title}</h3>
            <small>Posted by <i>{props.data.markdownRemark.frontmatter.author}</i>  on {props.data.markdownRemark.frontmatter.date} </small> 
            <br />
            <br />
            <hr />
            
            <div dangerouslySetInnerHTML={{__html: props.data.markdownRemark.html}}></div>
            <b>Tags: </b> {(props.data.markdownRemark.frontmatter.tags).map(app => (
                                    `#${app} `
                                ))}
            <br />
            <b>Share:</b>
            <Share
				socialConfig={{
					twitterHandle: props.data.site.siteMetadata.twitterHandle,
					config: {
						url: `${props.data.site.siteMetadata.url}/${props.data.markdownRemark.fields.slug}`,
					},
				}}
				tags={props.data.markdownRemark.frontmatter.tags}
			/>
        </Layout>
    )
}

export default BlogPost